<template>
	<v-form v-model="valid" ref="form">
		<span class="subtitle-1">E-mail</span>
		<v-text-field
			outlined
			dense
			v-model="dataLogin.email"
			:rules="emailRule"
		></v-text-field>
		<span class="subtitle-1">Password</span>
		<v-text-field
			:append-icon="showpassword ? 'mdi-eye' : 'mdi-eye-off'"
      :type="showpassword ? 'text' : 'password'"
      @click:append="showpassword = !showpassword"
			outlined
			dense
			v-model="dataLogin.password"
			:rules="requiredRule"
		></v-text-field>
		<Notif :msg="msg" :status="status" @visible="visible" />
		<div class="d-flex">
			<v-btn
				depressed
				:loading="isLoading"
				color="purple"
				type="submit"
				dark
				@click.prevent="validate">
				login
			</v-btn>
			<v-spacer></v-spacer>
			<div class="mt-2">
				Don't have an account ? 
				<span 
					class="pointer purple--text"
					@click="$router.push({ name: 'register-startup' })">
					Register
				</span>
			</div>
		</div>
	</v-form>
</template>

<style>
	.pointer{ cursor: pointer; }
</style>

<script>
	import { mapState } from 'vuex'
	import Notif from '@/components/Notif'

	export default{
		data: () => ({
			valid: true,
			dataLogin: {
				email: '',
				password: ''
			},
			requiredRule: [ v => !!v || 'Data harus diisi' ],
			msg: {
				success: 'Login berhasil',
				error: 'Login gagal',
				visible: false
			},
			showpassword: false
		}),

		components: { Notif },

		computed: {
			...mapState('dataLogin', ['isLoading', 'status']),
			emailRule () {
				return [
          ...this.requiredRule,
          v => /.+@.+\..+/.test(v) || 'Data berupa e-mail'
        ]
			}
		},

		methods: {
			validate () {
				if (this.$refs.form.validate()) {
					this.$store.dispatch('dataLogin/postLoginStartup', this.dataLogin)
					this.$store.dispatch('dataLogin/updateIsLoading', true)
					// this.msg.visible = true
				}
			},
			visible(val) {
				this.msg.visible = val
				this.$store.commit('dataLogin/updateStatus', null)
			}
		}
	}
</script>